body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html {
  height: 100vh;
  background: white;
  margin: 0;
  padding: 0; }

* {
  box-sizing: border-box; }

.app {
  height: 100vh; }

.hp__page {
  font-family: 'Gill Sans', 'Gill Sans MT', 'Calibri', 'Trebuchet MS', sans-serif;
  margin: 0, auto;
  height: 100%;
  background-image: url(../../static/media/bg.717c01a0.png);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  overflow: scroll; }

.hp__header {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgba(62, 4, 5, 0.8); }

.hp__header-title {
  margin: 0;
  padding: 20px;
  font-size: 60px;
  text-align: center;
  color: #EFAD00; }
  .hp__header-title .title-img {
    height: 50px; }

.hp__main {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  padding-top: 10px; }
  .hp__main .errorpage {
    width: 400px;
    background-image: url(../../static/media/marauder.cda511af.jpg);
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    -webkit-transform: translate(-28%, 0%);
            transform: translate(-28%, 0%); }
    .hp__main .errorpage p {
      color: #3e0405;
      font-weight: bold;
      text-transform: uppercase;
      font-size: 40px;
      text-shadow: 2px 2px 8px #EFAD00; }
    @media all and (min-width: 768px) {
      .hp__main .errorpage {
        width: 800px;
        font-size: 60px;
        -webkit-transform: translate(0%, 0%);
                transform: translate(0%, 0%); } }

.hp__filter {
  display: flex;
  justify-content: center;
  align-items: center; }

.hp__filter-input {
  margin: 0, 20px;
  width: 400px;
  height: 28px;
  text-align: center; }

.hp__detail {
  height: 100%;
  width: 100%;
  margin-top: 30px;
  display: flex;
  justify-content: center;
  align-items: center; }
  .hp__detail .errorpage {
    width: 500px;
    -webkit-transform: translate(14%, 65%);
            transform: translate(14%, 65%); }
    @media all and (min-width: 768px) {
      .hp__detail .errorpage {
        width: 650px;
        -webkit-transform: translate(10%, 65%);
                transform: translate(10%, 65%); } }

.hp__detail-card {
  background-color: #3e0405;
  box-shadow: 0px 1px 1px 3px #EFAD00;
  color: white;
  width: 400px;
  height: 300px;
  display: flex;
  position: relative; }
  @media all and (min-width: 768px) {
    .hp__detail-card {
      width: 600px;
      height: 300px; } }

.detail__card-databox {
  flex-grow: 1;
  padding: 10px;
  display: flex;
  flex-flow: column nowrap;
  justify-content: space-between; }

.detail__card-imagebox {
  height: 100%;
  overflow: hidden; }

.detail__card-image {
  height: 100%; }

.detail__card-name {
  margin: 0;
  align-self: center; }

.detail__card-house {
  margin: 0; }

.detail__card-born {
  margin: 0; }

.detail__card-patronus {
  margin: 0;
  text-transform: capitalize; }

.detail__card-status {
  margin: 0; }

.hp-detail-link {
  text-align: center;
  padding: 8px;
  height: 40px;
  background-image: linear-gradient(#6d2c2e, #3e0405);
  border-radius: 10px; }

.hp__container {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 30px; }

.hp__list {
  display: grid;
  grid-template-columns: 180px;
  grid-auto-rows: 400px;
  grid-gap: 20px;
  padding: 0; }
  @media all and (min-width: 768px) {
    .hp__list {
      grid-template-columns: 180px 180px 180px 180px; } }

.hp__list-item {
  background-color: grey;
  box-shadow: 0px 1px 1px 3px #EFAD00;
  list-style-type: none;
  color: white;
  display: grid;
  grid-template-rows: 240px 1fr;
  position: relative; }

.hp-list-link {
  background-image: linear-gradient(#6d2c2e, #3e0405);
  text-align: center;
  padding: 8px;
  height: 40px;
  border-radius: 10px; }

.list__card-databox {
  height: 160px;
  padding: 10px;
  background-color: #3e0405;
  display: flex;
  flex-flow: column nowrap;
  justify-content: space-between; }

.list-card-imagebox {
  height: 240px;
  overflow: hidden; }

.list__card-image {
  min-height: 100%;
  width: 100%; }

.list__card-name {
  margin: 0; }

.list__card-house {
  margin: 0; }

.card__container {
  display: flex; }

.detail__card-icon {
  position: relative;
  -webkit-transform: translate(5px, -5px);
          transform: translate(5px, -5px);
  height: 24px; }

.patronus__container-nop {
  display: flex; }

.patronus-nop {
  height: 28px;
  position: relative;
  -webkit-transform: translate(5px, -5px);
          transform: translate(5px, -5px); }

.house-nop {
  height: 26px; }

.list__house-icon {
  position: absolute;
  -webkit-transform: translateX(-100%);
          transform: translateX(-100%);
  height: 24px; }

